// 获取个人信息
import { getProfileData } from '@/network/getProfileData'

// 导入vuex
import { mapGetters } from 'vuex'
// 导入工具类
import { replaceImage } from '@/utils/dealUrl'

// 导入公共类
import { HOSTWWW } from '@/common/const'
export default{
	data(){
		return{
			mytrue:false,//是否登录
			username:"",
			avatar:"",
			integral:"",
			now_money:"",
			sex:"",
			product_collect:"",
			shop_collect:"",
			visit:""
		}
	},
	onShow() {
		if(this.isToken){
			getProfileData(this.isToken).then(res => {
				const data = res.data
				if(data.code == 400){
					uni.showToast({
						title:'登录失败',
						icon:'none'
					})
				}else if(data.code == 401){
					uni.showToast({
						title:'登录超时',
						icon:'none'
					})
				}else if(data.code == 200){
					console.log(res)
					this.mytrue = true
					this.avatar = HOSTWWW + data.data.avatar
					this.username = data.data.nickname
					this.integral = data.data.integral
					this.now_money = data.data.now_money
					this.sex = data.data.sex
					this.product_collect = data.data.product_collect
					this.shop_collect = data.data.shop_collect
					this.visit = data.data.visit
				}
				
			})
		}else{
			console.log('您还未登录')
		}
	},
	onNavigationBarButtonTap(e){
		console.log("监听到原生标题栏按钮点击事件");
		console.log(e);
		if(e.index == 0){
			//消息
			uni.navigateTo({
				url:'Inform/inform'
			})
		}else{
			//设置
			uni.navigateTo({
				url:'Seeting/seeting'
			})
		}
	},
	methods:{
		//点击登录
		loginClick(){
			uni.navigateTo({
				url:'../login/login'
			})
		},
		//商品收藏
		collectClick(){
			uni.navigateTo({
				url:'MyshopMessage/collect'
			})
		},
		//店铺收藏
		attrntionClick(){
			uni.navigateTo({
				url:'MyshopMessage/attention'
			})
		},
		//积分商城
		integralShopCick(){
			uni.navigateTo({
				url:'../Home/Integral/integralShop'
			})
		},
		//优惠券
		discountsClick(){
			uni.navigateTo({
				url:'Discounts/mydiscounts'
			})
		},
		//我的订单
		myOrderClick(num){
			uni.navigateTo({
				url:'MyOrder/myorder?index=' + num
			})
		},
		//退款
		refundClick(){
			uni.navigateTo({
				url:'MyBooking/refund/refund'
			})
		},
		//我的拼团
		MybookingClick(){
			uni.navigateTo({
				url:'MyBooking/mybooking'
			})
		},
		//商家入驻
		enterClick(){
			uni.navigateTo({
				url:'Enter/enter'
			})
		},
		//我的地址
		addressClick(){
			uni.navigateTo({
				url:'address/AllAddress'
			})
		},
		//积分明细
		intrgralDetailClick(){
			uni.navigateTo({
				url:'../Home/Integral/intrgralDetail'
			})
		},
		//领券中心
		discountscententClick(){
			uni.navigateTo({
				url:'Discounts/DiscpuntsCentent'
			})
		}
	},
	computed:{
		...mapGetters(['isToken'])
		
	}
	
}
